.App{
  height: 100vh;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(153,218,255,1)), to(rgba(0,128,128,1)));
  background: -webkit-linear-gradient(top, rgba(153,218,255,1) 0%, rgba(0,128,128,1) 100%);
  background: -o-linear-gradient(top, rgba(153,218,255,1) 0%, rgba(0,128,128,1) 100%);
  background: linear-gradient(180deg, rgba(153,218,255,1) 0%, rgba(0,128,128,1) 100%);
}
.list{
  margin: 0;
  padding: 0;
}

.list__item{
  list-style: none;
}

.menu{
  text-align: center;
  display: -ms-flex;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  height: 100vh;
}
.menu li{
  list-style: none;
}
.menu__link{
  font-size: 18px;
  color: #fff;
  text-shadow: 0px 0px 1px rgba(0,0,0,0.3), 0 0 1px rgba(0,0,0,0.5); /* Параметры тени */
  text-transform: uppercase;
  text-decoration: none;
  line-height: 2;
  font-weight: 300;
  display: block;
  text-align: center;
  position: relative;
  bottom: 150px
}

.body{
  height: 100vh;
}

.body-this{
  display: -ms-flex;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  height: 100%
}

.body-this .menu__link{
  position: absolute;
  bottom: 20%;
}
.gameplace{
  margin-top: 40px;
  display: -ms-flex;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}
.item{
  display: block;
  width: 100px;
  height: 100px;
  border: 1px solid #000;
  margin: 1px;
  cursor: pointer;
}
.item img{
  width: 100%;
  height: 100%;
}
.item:hover{
  border: 1px solid #fff
}
.item--flip{
  border: 1px sold green
}
.item--picked{
  border: 1px solid green
}
.current-word{
  text-transform: uppercase;
}
.table-body{
  display: grid;
  grid-template-columns: 100px 100px 100px 100px 100px;
}
.table-body .aplha__li{
  margin: 5px 0;
}

.codes span{
  margin-right: 10px;
}
.codes span:nth-last-child(1){
  margin-right: 0;
}

.timer{

}
.timer p{
  display: inline-block;
}
.timer span{
  display: inline-block;
  min-width: 20px
}



